import React from "react";
import Lottie from "lottie-react-web";
import Animation from "./step2.json";
import { NavLink } from "react-router-dom";
import ReactAudioPlayer from "react-audio-player";

class AboutPage extends React.Component {
  componentDidMount() {
    window.scrollTo(0, 0);
    scrollTo(0, 0);
  }
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <div>
        <div className="center zero container">
          <div className="center zero row">
            <div className="center zero col-12">
              <div className="lottie">
                <ReactAudioPlayer
                  src="https://example-files.online-convert.com/audio/ogg/example.ogg"
                  autoPlay={true}
                  loop={true}
                  controls
                />
                <Lottie
                  options={{
                    animationData: Animation,
                    loop: true
                  }}
                />
                <Lottie
                  options={{
                    animationData: Animation,
                    loop: true
                  }}
                />
                <Lottie
                  options={{
                    animationData: Animation,
                    loop: true
                  }}
                />
              </div>
            </div>
            <p>
              <button onClick={this.props.previousStep}>Previous Step</button>
            </p>
            <p>
              <button onClick={this.props.nextStep}>Next Step</button>
            </p>
            <p>
              <button onClick={() => this.props.goToStep(2)}>Step 2</button>
            </p>
            <p>
              <button onClick={this.props.firstStep}>First Step</button>
            </p>
            <p>
              <button onClick={this.props.lastStep}>Last Step</button>
            </p>
          </div>
        </div>
      </div>
    );
  }
}

export default AboutPage;

