import React from "react";
import { NavLink } from "react-router-dom";
import ReactAudioPlayer from "react-audio-player";
import HomeImg from "./home1.png";
import StepWizard from "react-step-wizard";
import Step1 from "../Step1/step1.jsx";
import Step2 from "../Step2/step2.jsx";

class AboutPage extends React.Component {
  componentDidMount() {
    window.scrollTo(0, 0);
  }
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div>
        <div>
          <ReactAudioPlayer
            src="https://file-examples.com/wp-content/uploads/2017/11/file_example_OOG_2MG.ogg"
            autoPlay
            loop={true}
          />
        </div>
        <div>
          <StepWizard>
            <Step1 />
            <Step2 />
          </StepWizard>
        </div>
      </div>
    );
  }
}

